@import 'variables.scss';
@import 'libs/normalize';
@import 'components/all';
body {
    background-color: $background;
}

.wrapper {
    width: calc(100vw - 100px);
    height: 100%;
    background-color: #fff;
    margin: 50px auto;
    border-radius: 10px;
    max-width: 1400px;
}

.content {
    padding: 40px 0;
    &__title {
        margin: 35px 0;
    }
    &__items {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        grid-template-rows: repeat(2, 1fr);
        grid-column-gap: 11px;
        @media (max-width: 1440px) {
            grid-template-columns: repeat(3, 1fr);
        }
        @media (max-width: 1060px) {
            grid-template-columns: repeat(2, 1fr);
        }
        @media (max-width: 730px) {
            grid-template-columns: 1fr;
        }
    }
    &__top {
        display: flex;
        align-items: center;
        justify-content: space-between;
        @media (max-width: 1300px) {
            flex-direction: column;
            row-gap: 20px;
        }
    }
}

.container {
    width: $container-width;
    margin: 0 auto;
    &--cart {
        max-width: 820px;
        margin: 90px auto;
        .content__title {
            margin: 0;
        }
    }
}

.loaderContainer {
    display: flex;
    justify-content: center;
}

.cart {
    &__top {
        display: flex;
        justify-content: space-between;
        align-items: center;
        @media (max-width: 500px) {
            flex-direction: column;
            gap: 20px;
        }
    }
    .content__title {
        display: flex;
        align-items: center;
        font-size: 32px;
        svg {
            position: relative;
            top: -2px;
            width: 30px;
            height: 30px;
            margin-right: 10px;
            path {
                stroke: $black;
                strokewidth: 1.9;
            }
        }
    }
    .content__items {
        display: flex;
        flex-direction: column;
    }
    &__clear {
        display: flex;
        align-items: center;
        cursor: pointer;
        @include noselect();
        span {
            display: inline-block;
            margin-left: 7px;
            color: #b6b6b6;
            font-size: 18px;
        }
        span,
        svg,
        path {
            transition: all $duration ease-in-out;
        }
        &:hover {
            svg {
                fill: darken($color: #b6b6b6, $amount: 50);
            }
            span {
                color: darken($color: #b6b6b6, $amount: 50);
            }
        }
    }
    &__item {
        /* display: flex;
        width: 100%; */
        display: grid;
        /* grid-template-columns: 1fr 3fr 1fr 2fr 1fr; */
        grid-template-areas: 'img info info info count price price remove';
        border-top: 1px solid $gray-line;
        padding-top: 30px;
        margin-top: 30px;
        @media (max-width: 645px) {
            /* grid-template-columns: 1fr 2fr 1fr; */
            grid-template-areas: 'img info info info info' 'count price price remove remove';
        }
        &-img {
            display: flex;
            grid-area: img;
            align-items: center;
            max-width: 95px;
            @media (max-width: 645px) {
                img {
                    width: 60px;
                    height: 60px;
                }
                justify-content: center;
            }
            img {
                width: 80px;
                height: 80px;
            }
        }
        &-info {
            display: flex;
            grid-area: info;
            flex-direction: column;
            justify-content: center;
            max-width: 300px;
            h3 {
                font-weight: bold;
                font-size: 22px;
                line-height: 27px;
                letter-spacing: 0.01em;
            }
            p {
                font-size: 18px;
                color: #8d8d8d;
            }
            @media (max-width: 380px) {
                h3 {
                    font-size: 18px;
                }
                p {
                    font-size: 13px;
                }
            }
        }
        &-count {
            display: flex;
            align-items: center;
            grid-area: count;
            max-width: 95px;
            justify-content: space-between;
            &-minus {
                svg {
                    path:first-of-type {
                        display: none;
                    }
                }
            }
            b {
                font-size: 22px;
            }
            @media (max-width: 380px) {
                b {
                    font-size: 16px;
                }
            }
        }
        &-price {
            display: flex;
            align-items: center;
            justify-content: center;
            grid-area: price;
            /* width: 33%; */
            b {
                font-weight: bold;
                font-size: 22px;
                letter-spacing: 0.01em;
            }
            @media (max-width: 380px) {
                b {
                    font-size: 16px;
                }
            }
        }
        &-remove {
            display: flex;
            grid-area: remove;
            align-items: center;
            justify-content: flex-end;
            /* width: 4%; */
            .button {
                border-color: darken($color: $gray-line, $amount: 10);
            }
            svg {
                transform: rotate(45deg);
                path {
                    fill: darken($color: $gray-line, $amount: 15);
                }
            }
            .button {
                svg {
                    width: 11.5px;
                    height: 11.5px;
                    position: relative;
                }
                &:hover,
                &:active {
                    border-color: darken($color: $gray-line, $amount: 80);
                    background-color: darken($color: $gray-line, $amount: 80);
                }
            }
        }
    }
    &__bottom {
        margin: 50px 0;
        &-details {
            display: flex;
            justify-content: space-between;
            span {
                font-size: 22px;
                &:last-of-type {
                    b {
                        color: $orange;
                    }
                }
            }
        }
        &-buttons {
            display: flex;
            justify-content: space-between;
            margin-top: 40px;
            .go-back-btn {
                display: flex;
                align-items: center;
                justify-content: center;
                width: 210px;
                border-color: darken($color: $gray-line, $amount: 10);
                span {
                    color: darken($color: $gray-line, $amount: 20);
                    font-weight: 500;
                    font-weight: 600;
                }
                &:hover {
                    background-color: darken($color: $gray-line, $amount: 90);
                    border-color: darken($color: $gray-line, $amount: 90);
                    span {
                        color: $gray-line;
                    }
                }
                svg {
                    margin-right: 12px;
                    path {
                        fill: transparent;
                        strokewidth: 2;
                    }
                }
            }
            .pay-btn {
                font-size: 16px;
                font-weight: 600;
                width: 210px;
                padding: 16px;
            }
        }
        @media (max-width: 645px) {
            display: flex;
            flex-direction: column;
            gap: 20px;
            &-details,
            &-buttons {
                display: flex;
                flex-direction: column-reverse;
                gap: 20px;
                margin-top: 0;
            }
        }
    }
    &--empty {
        margin: 0 auto;
        width: 560px;
        text-align: center;
        h2 {
            font-size: 32px;
            margin-bottom: 10px;
        }
        p {
            font-size: 18px;
            line-height: 145.4%;
            letter-spacing: 0.01em;
            color: #777777;
        }
        icon {
            position: relative;
            top: 2px;
        }
        img {
            display: block;
            width: 300px;
            margin: 45px auto 60px;
        }
        .button--black {
            padding: 12px 0 14px;
            width: 230px;
            margin: 0 auto;
            font-weight: 600;
            font-size: 18px;
        }
    }
}