.pagination {
    li {
        display: inline-flex;
        justify-content: center;
        align-items: center;
        &.selected {
            a {
                background-color: #fe5f1e;
                color: #fff;
            }
        }
        a {
            display: flex;
            color: #fe5f1e;
            margin-right: 10px;
            cursor: pointer;
            align-items: center;
            justify-content: center;
            width: 40px;
            height: 40px;
            padding: 10px;
            border-radius: 30px;
            border: 1px solid #fe5f1e;
            &:hover {
                background-color: #fe5f1e;
                color: #fff;
            }
        }
    }
}