@import '../variables';
.categories {
    @media (max-width: 1300px) {
        width: 100%;
    }
    ul {
        display: flex;
        @media (max-width: 1300px) {
            justify-content: center;
            row-gap: 20px;
            flex-wrap: wrap;
        }
        li {
            background-color: #f9f9f9;
            padding: 13px 30px;
            border-radius: 30px;
            margin-right: 10px;
            font-weight: bold;
            cursor: pointer;
            transition: background-color 0.1s ease-in-out;
            @include noselect();
            &:hover {
                background-color: darken(#f9f9f9, 2%);
            }
            &:active {
                background-color: darken(#f9f9f9, 5%);
            }
            &.active {
                background-color: #282828;
                color: #fff;
            }
            @media (max-width: 1024px) {
                padding: 12px 25px;
                border-radius: 26px;
            }
        }
    }
}