@import '../variables.scss';
.header {
    border-bottom: 1px solid $gray-line;
    padding: 40px 0;
    .container {
        display: grid;
        grid-template-areas: 'logo logo search search search cart cart';
        align-items: center;
        row-gap: 14px;
        justify-content: space-between;
        @media (max-width: 1024px) {
            grid-template-areas: 'logo logo . cart cart' '. search search search .';
        }
        @media (max-width: 740px) {
            grid-template-areas: 'logo logo logo logo logo' ' search search search  cart cart';
        }
        @media (max-width: 603px) {
            grid-template-areas: 'logo' 'search' 'cart';
        }
    }
    &__logo {
        display: flex;
        grid-area: logo;
        img {
            margin-right: 15px;
        }
        h1 {
            color: #181818;
            font-size: 24px;
            letter-spacing: 1%;
            text-transform: uppercase;
            font-weight: 800;
        }
        p {
            color: #7b7b7b;
        }
    }
    &__cart {
        display: flex;
        justify-content: end;
        grid-area: cart;
        @media (max-width: 603px) {
            justify-content: start;
        }
    }
    &__search {
        grid-area: search;
        label {
            div {
                display: flex;
                align-items: center;
            }
            display: flex;
            align-items: center;
            justify-content: space-between;
            border-radius: 9px;
            padding: 12px 20px 12px 10px;
            outline: none;
            border: 1px solid rgba(0, 0, 0, 0.1);
            width: 90%;
            svg {
                width: 24px;
                height: 24px;
                cursor: pointer;
            }
            @media (max-width: 603px) {
                width: 100%;
            }
        }
        input {
            font-size: 16px;
            margin-left: 10px;
            outline: none;
            border: none;
        }
    }
}